import React from "react";
import { Fragment, useRef } from "react";
import { Spinner } from "flowbite-react";
import { Dialog, Transition } from "@headlessui/react";
import {
  CheckCircleIcon,
  ExclamationTriangleIcon,
} from "@heroicons/react/24/outline";

const Modal = ({
  open,
  setOpen,
  onConfirm,
  isLoading,
  isSuccess,
  isFailed,
  responseMessage,
}) => {
  const cancelButtonRef = useRef(null);

  return (
    <Transition.Root show={open} as={Fragment}>
      <Dialog
        as="div"
        className="relative z-10"
        initialFocus={cancelButtonRef}
        onClose={!isLoading ? setOpen : () => {}}
      >
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
        </Transition.Child>

        <div className="fixed inset-0 z-10 overflow-y-auto">
          <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
              enterTo="opacity-100 translate-y-0 sm:scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 translate-y-0 sm:scale-100"
              leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
            >
              <Dialog.Panel className="relative transform overflow-hidden rounded-lg bg-white text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg">
                <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                  <div className="sm:flex sm:items-start">
                    <div className="w-full pt-4 pb-4 text-center sm:mt-0 sm:ml-4 sm:text-left">
                      {isLoading && (
                        <div className="mb-8 mt-4 flex animate-pulse items-center justify-center">
                          <Spinner color="failure" size="xl" />
                        </div>
                      )}
                      {isSuccess && (
                        <div className="mb-8 mt-4 flex items-center justify-center">
                          <CheckCircleIcon
                            className="h-12 w-12 text-green-600"
                            aria-hidden="true"
                          />
                        </div>
                      )}
                      {isFailed && (
                        <div className="mb-8 mt-4 flex items-center justify-center">
                          <ExclamationTriangleIcon
                            className="h-12 w-12 text-red-600"
                            aria-hidden="true"
                          />
                        </div>
                      )}
                      <Dialog.Title
                        as="h3"
                        className={
                          "text-lg font-medium leading-6 text-gray-900" +
                          (isLoading || isSuccess || isFailed
                            ? " text-center"
                            : "")
                        }
                      >
                        {!isLoading && !isSuccess && !isFailed && "Konfirmasi"}
                        {isLoading && "Sedang mengirimkan data..."}
                        {isSuccess && "Data berhasil dikirim!"}
                        {isFailed && responseMessage}
                      </Dialog.Title>
                      <div className="mt-4">
                        <p className="text-md text-gray-500">
                          {!isLoading &&
                            !isSuccess &&
                            !isFailed &&
                            "Pastikan semua data yang ingin dikirimkan sudah terisi dengan benar"}
                          {isSuccess &&
                            "Silakan cek dan print out nomor ujian yang telah dikirimkan ke email Anda. Apabila tidak menerima email di folder inbox, silakan cek pada folder spam."}
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="bg-gray-50 px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6">
                  {!isLoading && !isSuccess && !isFailed && (
                    <>
                      <button
                        type="button"
                        className="inline-flex w-full justify-center rounded-md border border-transparent bg-red-600 px-4 py-2 text-base font-medium text-white shadow-sm hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-offset-2 sm:ml-3 sm:w-auto sm:text-sm"
                        onClick={() => onConfirm()}
                      >
                        Kirim
                      </button>
                      <button
                        type="button"
                        className="mt-3 inline-flex w-full justify-center rounded-md border border-gray-300 bg-white px-4 py-2 text-base font-medium text-gray-700 shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm"
                        onClick={() => setOpen(false)}
                        ref={cancelButtonRef}
                      >
                        Kembali
                      </button>
                    </>
                  )}
                  {(isSuccess || isFailed) && (
                    <button
                      type="button"
                      className="inline-flex w-full justify-center rounded-md border border-transparent bg-red-600 px-4 py-2 text-base font-medium text-white shadow-sm hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-offset-2 sm:ml-3 sm:w-auto sm:text-sm"
                      onClick={() => {
                        setOpen(false);
                      }}
                    >
                      Ok
                    </button>
                  )}
                </div>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition.Root>
  );
};

export default Modal;
